.card {
	width: 100%;
	border-radius: 20px;
	display: flex;
	flex-direction: column;
	background: var(--bgCard);
	border: 1px solid var(--borderColor);
	overflow: hidden;
	height: 280px;
	padding: 15px 40px;

	.list {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;

		.item {
			display: flex;
			align-items: center;
			color: var(--sTextLight);
			font-size: 15px;
			font-style: normal;
			font-weight: 400;
			line-height: 22px;
			padding-top: 12px;
			padding-bottom: 12px;

			span {
				// background: green;
				width: 100%;
				max-width: 70%;
				text-align: left;
			}

			strong {
				color: var(--sText);
			}

			&:not(:last-child) {
				border-bottom: 1px dashed var(--borderColor);
			}
		}
	}
}
