.card {
	width: 100%;
	border-radius: 20px;
	display: flex;
	flex-direction: column;
	background: var(--sPrimary);
	border: 1px solid var(--borderColor);
	overflow: hidden;
	height: 280px;

	.head {
		padding: 15px 30px;
		font-size: 18px;
		font-weight: 700;
		line-height: 22px;
		font-style: normal;
		color: var(--sWhite);
	}

	.body {
		flex: 1;
		padding: 15px;
		border-radius: 20px;
		background: var(--sWhite);
	}
}
